import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { RemoteService } from '@app/remote.service';
import { CookieService } from 'ngx-cookie-service';
import { MatPaginator, PageEvent } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { FormControl } from '@angular/forms';
export interface auditlist {
  status: any;
  date: any;
  position: any;
  comment: any;
 
}
const audit_Data: auditlist[] = [
  { position: 1, status: 'Ready to Audit', date: '13-03-2023', comment: '-'},
  { position: 2, status: 'Confirm payment', date: '13-03-2023', comment: '-'},
  { position: 3, status: 'Missing files', date: '13-03-2023', comment: '-'},

];
@Component({
  selector: 'app-centreaudit',
  templateUrl: './centreaudit.component.html',
  styleUrls: ['./centreaudit.component.scss'],
  encapsulation: ViewEncapsulation.None,

})
export class CentreauditComponent implements OnInit {
  i18n(key) {
    return this.translate.instant(key);
  }
  auditdatalist = ['status', 'date', 'comment',  'action'];
  auditData = new MatTableDataSource<auditlist>(audit_Data);
  hidefilder: boolean = true;
  filtername = "Hide Filter";
  @ViewChild("paginator") paginator: MatPaginator;
  public pages: any;
  page: number = 10;
  bronze: boolean = false;
  gold: boolean = false;
  resultsLength: number = 10;
  constructor(private translate: TranslateService,
    private remoteService: RemoteService,
    private cookieService: CookieService,) { }

    languagelist = [{ "id": "1", "languageName": "English", "languagecode": "en", "CountryMst_Pk": "136", "dir": "ltr" },
    { "id": "2", "languageName": "Arabic", "languagecode": "ar", "CountryMst_Pk": "31", "dir": "rtl" }]
    dir = 'ltr';

  ngOnInit(): void {
   if (this.cookieService.get('languageCookieId') && this.cookieService.get('languageCookieId') != undefined && this.cookieService.get('languageCookieId') != null) {
      const toSelect = this.languagelist.find(c => c.id === this.cookieService.get('languageCookieId'));
      //this.patientCategory.get('patientCategory').setValue(toSelect);
      this.translate.setDefaultLang(toSelect.languagecode);
      this.dir = toSelect.dir;
      if(toSelect.languagecode == 'en'){
        this.filtername = "Hide Filter";
      }else{
        this.filtername = "sh Filter";      }
    } else {
      const toSelect = this.languagelist.find(c => c.id == '1');
      //this.patientCategory.get('patientCategory').setValue(toSelect);
      this.translate.setDefaultLang(toSelect.languagecode);
      this.dir = toSelect.dir;
      this.filtername = "Hide Filter";
    }
    this.remoteService.getLanguageCookie().subscribe(data => {
      this.translate.setDefaultLang(this.cookieService.get('languageCode'));
      if (this.cookieService.get('languageCookieId') && this.cookieService.get('languageCookieId') != undefined && this.cookieService.get('languageCookieId') != null) {
        const toSelect = this.languagelist.find(c => c.id === this.cookieService.get('languageCookieId'));
        //this.patientCategory.get('patientCategory').setValue(toSelect);
        this.translate.setDefaultLang(toSelect.languagecode);
        this.dir = toSelect.dir;
        if(toSelect.languagecode == 'en'){
          this.filtername = "Hide Filter";
        }else{
          this.filtername = "sh Filter";      }
      } else {
        const toSelect = this.languagelist.find(c => c.id == '1');
        //this.patientCategory.get('patientCategory').setValue(toSelect);
        this.translate.setDefaultLang(toSelect.languagecode);
        this.dir = toSelect.dir;
        this.filtername = "Hide Filter";
      }
    });
  }
  clickEvent() {

    this.hidefilder = !this.hidefilder;
    if (!this.hidefilder) {
      this.filtername = this.i18n('company.showfilt');
      const id = document.getElementById('searchrow') as HTMLElement;
      id.style.display = 'none';

    } else {
      this.filtername = this.i18n('company.hitefil');
      const id = document.getElementById('searchrow') as HTMLElement;
      id.style.display = 'flex';

    }
  }
  syncPrimaryPaginator(event: PageEvent) {
    this.paginator.pageIndex = event.pageIndex;
    this.paginator.pageSize = event.pageSize;
    this.page = event.pageSize;
  }
  appl_form = new FormControl('');
  date_expiry = new FormControl('');
}
