import { Component, Input, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { MatTabGroup } from '@angular/material/tabs';
import { TranslateService } from '@ngx-translate/core';
import { RemoteService } from '@app/remote.service';
import { CookieService } from 'ngx-cookie-service';
import { ActivatedRoute } from '@angular/router';
import { CentreinstituteinfoComponent } from '../centreinstituteinfo/centreinstituteinfo.component';
import swal from 'sweetalert';
import { Router } from '@angular/router';
import { ApplicationService } from '@app/services/application.service';

@Component({
  selector: 'app-centredesktopreview',
  templateUrl: './centredesktopreview.component.html',
  styleUrls: ['./centredesktopreview.component.scss'],
  encapsulation: ViewEncapsulation.None,

})
export class CentredesktopreviewComponent implements OnInit {
  bronze: boolean = false;
  gold: boolean = false;
  isValidated: boolean = false;
  @ViewChild('MatTabGroup') tabGroup: MatTabGroup;
  @Input() mattab: number = 0;
  apptempPk: any;
  condition: boolean = true;
  tp_name: any;
  company_name: any;
  appdt_appreferno: any;
  appdt_submittedon: any;
  appdt_status: any;
  expandedElement: string;
  appdt_grademst_fk: any;
  type: any;
  appdt_certificategenon: any;
  appdt_certificateexpiry: any;
  isexpired: number;
  appiit_loclatitude: any;
  appiit_loclongitude: any;
  disableSubmitButton: boolean;
  appiit_officetype: string;
  appdt_apptype: string;

  i18n(key) {
    return this.translate.instant(key);
  }
  constructor(private translate: TranslateService,
    private remoteService: RemoteService,
    private cookieService: CookieService,public routeid: ActivatedRoute,private route: Router,private appservice : ApplicationService) {
      this.onValidation = this.onValidation.bind(this);
     

     }

    languagelist = [{ "id": "1", "languageName": "English", "languagecode": "en", "CountryMst_Pk": "136", "dir": "ltr" },
    { "id": "2", "languageName": "Arabic", "languagecode": "ar", "CountryMst_Pk": "31", "dir": "rtl" }]
    dir = 'ltr';
  
  ngOnInit(): void {
    
    if (this.cookieService.get('languageCookieId') && this.cookieService.get('languageCookieId') != undefined && this.cookieService.get('languageCookieId') != null) {
      const toSelect = this.languagelist.find(c => c.id === this.cookieService.get('languageCookieId'));
      //this.patientCategory.get('patientCategory').setValue(toSelect);
      this.translate.setDefaultLang(toSelect.languagecode);
      this.dir = toSelect.dir;
    } else {
      const toSelect = this.languagelist.find(c => c.id == '1');
      //this.patientCategory.get('patientCategory').setValue(toSelect);
      this.translate.setDefaultLang(toSelect.languagecode);
      this.dir = toSelect.dir;
    }
    this.remoteService.getLanguageCookie().subscribe(data => {
      this.translate.setDefaultLang(this.cookieService.get('languageCode'));
      if (this.cookieService.get('languageCookieId') && this.cookieService.get('languageCookieId') != undefined && this.cookieService.get('languageCookieId') != null) {
        const toSelect = this.languagelist.find(c => c.id === this.cookieService.get('languageCookieId'));
        //this.patientCategory.get('patientCategory').setValue(toSelect);
        this.translate.setDefaultLang(toSelect.languagecode);
        this.dir = toSelect.dir;
      } else {
        const toSelect = this.languagelist.find(c => c.id == '1');
        //this.patientCategory.get('patientCategory').setValue(toSelect);
        this.translate.setDefaultLang(toSelect.languagecode);
        this.dir = toSelect.dir;
        
      }
    });
 
    this.routeid.params.subscribe(params => {
      this.apptempPk = params['id'];
      this.type = params['type'];
      });
  }


  nexttab() {
    this.mattab = 1;
    
  }
  nextsecondtab() {
    this.mattab = 2;
  }
  previous() {
    this.mattab = 0;
  }
  previoustab() {
    this.mattab = 1;
  }
  nextthirdtab() {
    this.mattab =3;
  }
  previousfourtab() {
    this.mattab = 2;
  }
  nextfourtab(){
    this.mattab =4;
  }
  previousfifthtab(){
    this.mattab =3;
  }
  nextfifthtab(){
    this.mattab =5;
  }
  previoussixtab(){
    this.mattab =4;
  }
  nextsixtab(){
    this.mattab =6;
  }
  previousseventab(){
    this.mattab =5;
  }
  cancle() {
    swal({
      title: this.i18n('desktop.doyouwant'),
      text: '',
      icon: 'warning',
      buttons: [this.i18n('desktop.no'), this.i18n('desktop.yes')],
      dangerMode: true,
      closeOnClickOutside: false
    }).then((willGoBack) => {
      if (willGoBack) {
        this.disableSubmitButton = true;
        this.route.navigate(['/centrecertification/home']);
        setTimeout(() => {
          this.disableSubmitButton = false;
        }, 2000);
      }
    })
  }

  onValidation(form, resetForm){
    this.disableSubmitButton = true; 
    this.appservice.updateapplication(form.value , this.apptempPk).subscribe(data => {
      this.disableSubmitButton = false; 
      console.log(data.data.flag ,'flag');
      if(data.data.flag == 4){
        resetForm();
          swal({
            title:this.i18n('company.declpop') ,
            text: " ",
            icon: 'warning',
            buttons: [false, this.i18n('company.ok')],
            dangerMode: true,
            closeOnClickOutside: false
          }).then(() => {
            this.disableSubmitButton = true;
            this.route.navigate(['/centrecertification/home']);
            setTimeout(() => {
             this.disableSubmitButton = false;
           }, 5000);
          })
        }else if(data.data == false){

          resetForm();
          swal({
            title:'In order to complete the approval process for the certification form, please review and approve the section(s) that were declined.' ,
            text: " ",
            icon: 'warning',
            buttons: [false, this.i18n('company.ok')],
            dangerMode: true,
            closeOnClickOutside: false
          }).then(() => {
            this.disableSubmitButton = true;
            //this.route.navigate(['/centrecertification/home']);
            setTimeout(() => {
             this.disableSubmitButton = false;
           }, 5000);
          })

        }
        
        else{
          this.isValidated = true;  
          swal({
            title: this.i18n('company.succvali') ,
            text: " ",
            icon: 'success',
            buttons: [false, this.i18n('company.ok')],
            dangerMode: true,
            closeOnClickOutside: false
          }).then(() => {
            this.disableSubmitButton = true;
             this.route.navigate(['/centrecertification/home']);
             setTimeout(() => {
              this.disableSubmitButton = false;
            }, 5000);
          })
        }
                     
    });
   

}



getchilddata(data){
// this.tp_name = data.tpname_en;
// this.company_name = data.compname_en;


}

getappdata(data){
  console.log(data , 'testapp');
  this.appdt_appreferno = data.appdt_appreferno;
  this.appdt_submittedon = data.appdt_submittedon;
  this.appdt_status =  data.appdt_status;
  this.appdt_grademst_fk =  data.appdt_grademst_fk;
  this.appdt_certificategenon = data.appdt_certificategenon;
  this.appiit_officetype  = (data.appiit_officetype == 1)?'Main Office':'Branch Office';
  this.appdt_apptype     =   (data.appdt_apptype == 1)?'Initial' : (data.appdt_apptype == 2)?'Renewal': 'Updated';
//  this.appdt_certificateexpiry = data.appdt_certificateexpiry;
  this.appiit_loclatitude=  data.appiit_loclatitude;
  this.appiit_loclongitude = data.appiit_loclongitude;
  this.appdt_certificateexpiry= (data.appdt_certificateexpiry)?data.appdt_certificateexpiry:'-';
  var current_date = new Date();
  var specific_date = new Date(this.appdt_certificateexpiry); //Year, Month, Date    
  if (current_date.getTime() > specific_date.getTime()) {    
  this.isexpired = 1; 
  }else {    
    this.isexpired = 0;   
  } 
  this.tp_name = data.omrm_tpname_en;
  this.company_name = data.omrm_companyname_en;
   
  }
  tabClickFunctions = [
    // () =>  document.querySelector('.breadcrumb-item.active').innerHTML = 'Company Details',
    // () =>  document.querySelector('.breadcrumb-item.active').innerHTML = 'Institute Information',
    // () => this.expandedElement = '',
    // () =>  document.querySelector('.breadcrumb-item.active').innerHTML = 'Operator Contract Details',
    // () =>  document.querySelector('.breadcrumb-item.active').innerHTML = 'Document Required',
    // () =>  document.querySelector('.breadcrumb-item.active').innerHTML = 'Course',
    // () =>  document.querySelector('.breadcrumb-item.active').innerHTML = 'Staff',
  ];
  onTabSelect(event: any) {
    // const index = event.index;
    // this.tabClickFunctions[index]();
  }
}
