import { Component, OnInit, ViewChild, ViewEncapsulation ,EventEmitter, Output} from '@angular/core';
import { MatTabGroup } from '@angular/material/tabs';
import { TranslateService } from '@ngx-translate/core';
import { RemoteService } from '@app/remote.service';
import { CookieService } from 'ngx-cookie-service';
import { FormBuilder, FormGroup,FormArray, Validators, FormControl, FormGroupDirective, RequiredValidator } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { ApplicationService } from '@app/services/application.service';
import { ActivatedRoute,Route } from '@angular/router';

@Component({
  selector: 'app-siteaudit',
  templateUrl: './siteaudit.component.html',
  styleUrls: ['./siteaudit.component.scss'],
  encapsulation: ViewEncapsulation.None,

})
export class SiteauditComponent implements OnInit {
  // @ViewChild("infotab", { static: true }) mattab = 0;
  public mattab = 1;
  view: boolean = false;
  disableSubmitButton: boolean;
  // hideforcomments: boolean = false;
  hideforedites: boolean;
  hideforeditdata: boolean;
  appid: any;
  viewpage: any;
  appdt_appreferno: any;
  appiit_officetype: string;
  appdt_apptype: string;
  appdt_certificateexpiry: any;
  appdt_certificategenon: any;
  appdt_submittedon: any;
  appdt_updatedon: any;
  appdt_status: any;
  appiit_loclatitude: any;
  appiit_loclongitude: any;
  isexpired: number;
  ifarabic: boolean;
  omrm_companyname_en: any;
  omrm_tpname_en: any;
  sitecategory: any[];
  sitecategoryname: any[];
  sitequestions: any;
  questiondtls: any;
  questArr: any;
  ansArr: any;
  quest: any;
  ans: any;
  multichoisecount: any;
  categoryid: any;
  constructor(private translate: TranslateService,
    private remoteService: RemoteService,
    private cookieService: CookieService,public routeid: ActivatedRoute , private appservice : ApplicationService) { }

  
    languagelist = [{"id":"1","languageName":"English","languagecode":"en","CountryMst_Pk":"136","dir":"ltr"},
    {"id":"2","languageName":"Arabic","languagecode":"ar","CountryMst_Pk":"31","dir":"rtl"}];
    dir="ltr";;

  ngOnInit(): void {
    if (this.cookieService.get('languageCookieId') && this.cookieService.get('languageCookieId') != undefined && this.cookieService.get('languageCookieId') != null) {
      const toSelect = this.languagelist.find(c => c.id === this.cookieService.get('languageCookieId'));
      this.translate.setDefaultLang(toSelect.languagecode);
      this.dir = toSelect.dir;
    } else {
      const toSelect = this.languagelist.find(c => c.id == '1');
      this.translate.setDefaultLang(toSelect.languagecode);
      this.dir = toSelect.dir;
    }
    this.remoteService.getLanguageCookie().subscribe(data => {
      if (this.cookieService.get('languageCookieId') && this.cookieService.get('languageCookieId') != undefined && this.cookieService.get('languageCookieId') != null) {
        const toSelect = this.languagelist.find(c => c.id === this.cookieService.get('languageCookieId'));
        this.translate.setDefaultLang(toSelect.languagecode);
        this.dir = toSelect.dir;
      } else {
        const toSelect = this.languagelist.find(c => c.id == '1');
        this.translate.setDefaultLang(toSelect.languagecode);
        this.dir = toSelect.dir;
        if (toSelect.languagecode == 'en') {
          this.ifarabic = false;
        }
        else {
          this.ifarabic = true;
        }
      }
    });
    this.routeid.queryParams.subscribe(params => {
      this.appid = params['id'];
    });
    this.routeid.queryParams.subscribe(params => {
      this.viewpage = params['view'];
    });
    if(this.viewpage = 3) {
      this.hideforedites = false;
      this.hideforeditdata = true;
      console.log(1231234)
    }else {
      this.hideforedites = true;
      this.hideforeditdata = false;

    }
    this.sitedata();
    this.onCategoryTabClick(1);

  }
// next() {
//   this.mattab = 1;
//   console.log(true)
// }
previoussixtab() {
  this.mattab--;
}
getchilddata(data){
  console.log(data , 'matcheck');
  this.mattab++;
  }
 
onCategoryTabClick(id) {
  if(id) {
    this.appservice.getsitequestions(id).subscribe(data => {
      this.view = true;
      this.quest = data.data.data;
      this.ans   = data.data.Adata;
      this.questArr = this.quest.map(q => this.ans.filter(a => a.asaad_auditquestionmst_fk === q.asaad_auditquestionmst_fk));
      this.multichoisecount =  this.quest.filter(a => a.asaqm_questiontype == 2);
      this.multichoisecount =  this.multichoisecount.length;
      this.categoryid = id;
    });
  }
}

sitedata(){
  if(this.appid) {
    this.appservice.getsitedata(this.appid).subscribe(data => {
      this.appdt_appreferno = data.data.data[0].appasdt_applicationdtlstmp_fk;
        this.appiit_officetype  = (data.data.data[0].appiit_officetype == 1)?'Main Office':'Branch Office';
        this.appdt_apptype     =   (data.data.data[0].appdt_apptype == 1)?'Initial' : (data.data.data.appdt_apptype == 2)?'Renewal': 'Updated';
        this.appdt_certificateexpiry =     data.data.data[0].appdt_certificateexpiry;
        this.appdt_certificategenon =      data.data.data[0].appdt_certificategenon;
        this.appdt_submittedon    = data.data.data[0].appdt_submittedon;
        this.appdt_updatedon     = data.data.data[0].appdt_updatedon;
        this.appdt_status = data.data.data[0].appdt_status; 
        this.appiit_loclatitude =     data.data.data[0].appiit_loclatitude;
        this.appiit_loclongitude =      data.data.data[0].appiit_loclongitude; 
        var current_date = new Date();
        var specific_date = new Date(this.appdt_certificateexpiry); //Year, Month, Date   
        if (current_date.getTime() > specific_date.getTime()) {    
        this.isexpired = 1; 
        }else {    
          this.isexpired = 0;   
        }
      this.omrm_companyname_en        =      (this.ifarabic == true)?data.data.data[0].omrm_companyname_ar:data.data.data[0].omrm_companyname_en;
      this.omrm_tpname_en   =  (this.ifarabic == true)?data.data.data[0].omrm_tpname_ar:data.data.data[0].omrm_tpname_en;
      this.sitecategory  = data.data.data.map(d => ({id: d.appsiteauditreportcattmp_pk , name: d.asarct_categorytitle }))
    });
  }

}
}
