export class AppTempPagination {
  constructor(private http?: HttpClient) {
  }

  appGridUtil(sort: string, order: string, page: number, size: number,gridsearchValues?:string): Observable<any> {
    const href = environment.baseUrl + 'center/app-center/getdesktop';
    const sign = (order === 'desc') ? '-' : '';
    const requestUrl =
      `${href}?sort=${sign}${sort}&order=${order}&page=${page + 1}&size=${size}&gridsearchValues=${gridsearchValues}`;
    return this.http.get<any>(requestUrl, { headers: { Authorization: 'Bearer ' + localStorage.getItem('v3logindata') } });
  }
}
